body {
  margin: 0;
  padding: 0;
  background-color: #050e1f;
  background-image: url('./assets/bg_image.jpg');
  background-size: cover; /* Cover entire width and height */
  background-position: center top; /* Position at the top center */
  background-repeat: no-repeat;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* NavBar style */
.navbar {
  background-color: #050e1f;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 5px;
  display: flex;
  align-items: center;
  height: 30px; /* Adjust the height as needed */
}

.nav-links {
  /* Remove display: none; to always display the menu items */
  display: flex;
}

.nav-links.active {
  display: flex; /* Show the menu items when active */
}

.nav-links a {
  color: #b97f55; /* Set the color to white or any other desired color */
  padding: 10px 20px; /* Add some padding to the links */
  text-decoration: none; /* Remove underline */
}

.nav-links .active {
  padding: 10px 20px; /* Match padding with other links */
}

.nav-links a:hover {
  /* Optionally, define styles for when the link is hovered */
  text-decoration: underline; /* Add underline on hover if desired */
}
.submenu {
  display: flex;
  flex-direction: column;
  justify-content: flex-end; /* Align items to the right */
}

.menu-icon {
  display: none; /* Hide the menu icon by default */
  cursor: pointer;
}
  
.line {
  width: 30px;
  height: 3px;
  background-color: #b97f55;
  margin: 5px 0;
}

.active {
  background-color: #373e4c;
  width: auto; /* You can remove this line if not needed */
}

@media screen and (max-width: 768px) {
  .menu-icon {
    display: block;
    position: absolute;
    top: 5px;
    right: 10px;
  }

  .nav-links {
    flex-direction: column;
    align-items: center;
    position: absolute;
    padding-bottom: 10px;
    top: 30px;
    right: 10px;
    background-color: #050e1f;
    opacity: 0.9;
    width: fit-content;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: none;
    justify-content: left;
  }

  .nav-links.active {
    display: flex;
    width: max-content;
  }

  .nav-links.active a {
    text-align: center;
  }
}


.container {
  margin: 2%;
  background-color: #050e1f;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  border-radius: 10px;
  padding-bottom: 15px;
}
.container p {
  color: #ffffff;
  font-size: 20px;
  margin: 0;
  padding: 0;
  padding-top: 15px;
}
.container h1 {
  color: #ffffff;
  margin-top: 15px;
  padding: 5px;
  padding-top: 15px;
}
.container a {
  color: #b97f55;
  font-size: 20px;
  margin: 0;
  padding-right: 20px;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0 20px;
  margin: 2%;
  background-color: #373e4c;
  border-radius: 10px;
}

.media-gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  margin: 2%;
  margin-bottom: 10px;
  background-color: #373e4c;
  border-radius: 10px;
}
.media-gallery img {
  width: 33%;
  height: 33%;
  border-radius: 10px;
  padding: 5px;
}

@media screen and (max-width: 768px) {
  .media-gallery img {
    width: 100%; /* Set width to 100% for single item per row */
    height: auto; /* Ensure aspect ratio is maintained */
  }
}

@media screen and (min-width: 768px) {
  .media-gallery img {
    width: calc(33.33% - 20px * 2); /* Set width for three items in a row with padding */
    height: auto; /* Ensure aspect ratio is maintained */
    margin: 10px; /* Add margin to separate items */
    box-sizing: border-box; /* Include padding in the width calculation */
  }
}

/* Dropdown menu */
.dropdown {
  position: relative;
  display: inline-block;
  margin: 10px;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #050e1f;
  width: fit-content;
  margin-right: 100px;
  min-width: 160px;
  z-index: 1;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  outline: 1px solid #b97f55; /* Add an outline with 1px solid accent color */
  
  /* Adjust the positioning */
  left: -30px; /* Move the dropdown slightly to the left */
}

.dropdown-content a {
  color: #b97f55;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #373e4c;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .nav-links {
  background-color: #373e4c;
}

.char-image { /* make black and white */
  width: 25%;
  border-radius: 10px;
  padding: 5px;
  filter: grayscale(100%);
}

.location-image { /* make black and white */
  width: 80%;
  border-radius: 10px;
  padding: 5px;
}

.contact-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  margin: 2%;
  background-color: #373e4c;
  border-radius: 10px;
  padding-bottom: 15px;
}

.events {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: left;
  padding: 0 20px;
  margin: 2%;
  background-color: #373e4c;
  border-radius: 10px;
  padding-bottom: 15px;
}
.events li {
  color: #ffffff;
  margin: 5px;
  padding-top: 15px;
}

.forum-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: left;
  padding: 0 20px;
  margin: 2%;
  background-color: #373e4c;
}
.forum-list div {
  color: #ffffff;
  margin: 5px;
  padding-top: 15px;
  outline: 1px solid #000; /* Add an outline with 1px solid black */
}
.path {
  display: flex;
  flex-wrap: wrap;
  align-items: left;
  padding: 20px;
  padding-top: 10px;
  color: #ffffff;
  background-color: #373e4c;
  border-radius: 10px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* login modal */
.modal {
  background-color: #050e1f;
  color: #ffffff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.modal button {
  margin-top: 10px;
}
.positive-btn {
  background-color: #1c7035;
}
.cancel-btn {
  background-color: indianred;
}
.modal form {
  display: flex;
  flex-direction: column;
  justify-items: flex-start;
}
.modal input {
  margin-top: 5px;
}
.modal textarea {
  margin-top: 5px;
}
.success-message {
  color: #ffffff;
  padding: 10px;
  background-color: #1c7035;
  border-radius: 10px;
  margin: 2%;
}
